.hero-img {
    width: 100%;
    background: rgba(0,0,0,0.5);
    height: 60vh;
    position: relative;
}
.hero-img::before{
      content: "";
      background: url("https://images.unsplash.com/photo-1587620962725-abab7fe55159?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1031&q=80");
      background-size: cover;
      background-position: center top;
      height: 100%;
      width: 100%;
      position: absolute;
      top:0;
      left:0;
      z-index: -1;
}
.heading{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1{
    font-size: 2.4rem;
}
.hero-img h2{
    font-size: 1.4rem;
    text-align: center;
    color: white;
}

@media  screen and (max-width : 640px) {
    .hero-img h1{
        font-size: 2.4rem;
    }
    .hero-img h2{
        font-size: 1.4 rem;
        text-align: center;
        color: white;
    }
}